class LoggerService {
  constructor() {
    this.logLevel = process.env.NODE_ENV === 'production' ? 'error' : 'debug';
    this.logs = [];
    this.maxLogs = 100; // æœ€å¤§æ—¥å¿—æ¡æ•°
  }

  /**
   * è®°å½•è°ƒè¯•æ—¥å¿—
   * @param {string} message - æ—¥å¿—æ¶ˆæ¯
   * @param {object} data - é™„åŠ æ•°æ®
   */
  debug(message, data = null) {
    if (this.logLevel === 'debug') {
      this._log('DEBUG', message, data);
    }
  }

  /**
   * è®°å½•ä¿¡æ¯æ—¥å¿—
   * @param {string} message - æ—¥å¿—æ¶ˆæ¯
   * @param {object} data - é™„åŠ æ•°æ®
   */
  info(message, data = null) {
    if (['debug', 'info'].includes(this.logLevel)) {
      this._log('INFO', message, data);
    }
  }

  /**
   * è®°å½•è­¦å‘Šæ—¥å¿—
   * @param {string} message - æ—¥å¿—æ¶ˆæ¯
   * @param {object} data - é™„åŠ æ•°æ®
   */
  warn(message, data = null) {
    if (['debug', 'info', 'warn'].includes(this.logLevel)) {
      this._log('WARN', message, data);
    }
  }

  /**
   * è®°å½•é”™è¯¯æ—¥å¿—
   * @param {string} message - æ—¥å¿—æ¶ˆæ¯
   * @param {Error} error - é”™è¯¯å¯¹è±¡
   */
  error(message, error = null) {
    this._log('ERROR', message, error);
  }

  /**
   * å†…éƒ¨æ—¥å¿—è®°å½•æ–¹æ³•
   * @private
   */
  _log(level, message, data) {
    const timestamp = new Date().toISOString();
    const logEntry = {
      timestamp,
      level,
      message,
      data
    };

    // ä¿å­˜æ—¥å¿—
    this.logs.push(logEntry);
    if (this.logs.length > this.maxLogs) {
      this.logs.shift(); // ç§»é™¤æœ€æ—§çš„æ—¥å¿—
    }

    // æ§åˆ¶å°è¾“å‡º
    const logContent = `[${level}] ${timestamp} - ${message}`;
    switch (level) {
      case 'ERROR':
        console.error(logContent, data);
        break;
      case 'WARN':
        console.warn(logContent, data);
        break;
      case 'INFO':
        console.info(logContent, data);
        break;
      default:
        console.log(logContent, data);
    }
  }

  /**
   * è·å–æ‰€æœ‰æ—¥å¿—
   * @returns {array} æ—¥å¿—æ•°ç»„
   */
  getLogs() {
    return [...this.logs];
  }

  /**
   * æ¸…ç©ºæ—¥å¿—
   */
  clearLogs() {
    this.logs = [];
  }
}

// å¯¼å‡ºå•ä¾‹å®ä¾‹
export default new LoggerService();