import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/mappage',
    name: 'mappage',
    component: () => import('@/views/Mappage.vue')
  },
  {
    path: '/solution',
    name: 'Solution',
    component: () => import('@/views/Solution.vue')
  },
  {
    path: '/support',
    name: 'Support',
    component: () => import('@/views/Support.vue')
  },
  {
    path: '/manual',
    name: 'Manual',
    component: () => import('@/views/Manual.vue')
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('@/views/About.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/LoginPage.vue')
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('@/views/RegisterPage.vue')
  },
  {
    path: '/drone',
    name: 'Drone',
    component: () => import('@/views/Drone.vue')
  },
  {
    path: '/analyse',
    name: 'Analyse',
    component: () => import('@/views/Analyse.vue')
  },
  {
    path: '/green',
    name: 'Green',
    component: () => import('@/views/Green.vue')
  },
  {
    path: '/map',
    redirect: '/dashboard/map'
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: () => import('@/components/Dashboard.vue'),
    children: [
      {
        path: 'map',
        name: 'DashboardMap',
        component: () => import('@/components/DashboardMap.vue')
      },
      {
        path: 'field',
        name: 'DashboardField',
        component: () => import('@/views/FieldManagement.vue')
      },
      {
        path: 'task',
        name: 'DashboardTask',
        component: () => import('@/views/TaskManagement.vue')
      },
      {
        path: 'data',
        name: 'DashboardData',
        component: () => import('@/views/DataAnalysis.vue')
      },
      {        path: 'ai',        name: 'DashboardAI',        component: () => import('@/views/AIAnalysis.vue'),        children: [          {            path: 'disease',            name: 'DiseaseDetection',            component: () => import('@/views/AIDiseaseDetection.vue')          },          {            path: 'field',            name: 'FieldBoundaryAnalysis',            component: () => import('@/views/AIFieldBoundaryAnalysis.vue')          }        ],        redirect: '/dashboard/ai/disease'      },
      {
        path: 'drone',
        name: 'DashboardDrone',
        component: () => import('@/views/DroneControl.vue')
      },
      {
        path: 'settings',
        name: 'DashboardSettings',
        component: () => import('@/views/Settings.vue')
      },
      {
        path: 'logs',
        name: 'DashboardLogs',
        component: () => import('@/views/LogViewer.vue')
      }
    ],
    redirect: '/dashboard/map'
  },
  {
    path: '/work',
    name: 'WorkPage',
    component: () => import('@/views/WorkPage.vue')
  }
  
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router