<script>
export default {
  name: 'FloatingAIAssistant',
  data() {
    return {
      // æµ®åŠ¨åŠ©æ‰‹çŠ¶æ€
      floatingAssistant: null,
      isDragging: false,
      dragOffset: { x: 0, y: 0 },
      assistantHidden: false,
      assistantMinimized: false,
      isAnimating: false,

      
      // èŠå¤©çª—å£çŠ¶æ€
      showFloatingChat: false,
      chatWindowTop: 200,
      chatWindowLeft: 200,
      isMinimizing: false,
      
      // æ¶ˆæ¯çŠ¶æ€
      messages: [],
      userInput: '',
      isLoading: false,
      
      // æ‚¬æµ®æ°”æ³¡
      showBubble: false,
      bubbleText: '',
      bubbleLeft: 0,
      bubbleTop: 0,
      bubbleOpacity: 0,
      bubbleTimer: null,
      
      // æ™ºèƒ½æç¤º
      suggestionTimeout: null,
      suggestionTexts: [
        'æœ‰å†œä¸šé—®é¢˜ï¼Ÿéšæ—¶é—®æˆ‘ï¼',
        'éœ€è¦ç—…è™«å®³é˜²æ²»å»ºè®®å—ï¼Ÿ',
        'æƒ³äº†è§£æœ€æ–°çš„ç§æ¤æŠ€æœ¯ï¼Ÿ',
        'éœ€è¦æ–½è‚¥æ–¹æ¡ˆå’¨è¯¢ï¼Ÿ',
        'ä½œç‰©ç”Ÿé•¿å¼‚å¸¸ï¼Ÿå‘Šè¯‰æˆ‘ç—‡çŠ¶ï¼'
      ],
      
      // çª—å£å°ºå¯¸
      windowSize: { width: window.innerWidth, height: window.innerHeight }
    }
  },
  
  mounted() {
    // åˆå§‹åŒ–æµ®åŠ¨åŠ©æ‰‹
    this.$nextTick(() => {
      this.floatingAssistant = this.$refs.floatingAssistant;
      if (this.floatingAssistant) {
        this.floatingAssistant.style.bottom = '30px';
        this.floatingAssistant.style.right = '30px';
      }
    });
    
    // æ·»åŠ å…¨å±€äº‹ä»¶ç›‘å¬
    window.addEventListener('mousemove', this.onMouseMove);
    window.addEventListener('mouseup', this.onMouseUp);
    window.addEventListener('resize', this.handleResize);
    
    // æ–°çš„ç•Œé¢è®¾è®¡ä¸å†éœ€è¦è¡¨æƒ…åŠ¨ç”»
    
    // å¯åŠ¨æ™ºèƒ½æç¤º
    this.startSuggestions();
  },
  
  beforeUnmount() {
    // æ¸…ç†äº‹ä»¶ç›‘å¬
    window.removeEventListener('mousemove', this.onMouseMove);
    window.removeEventListener('mouseup', this.onMouseUp);
    window.removeEventListener('resize', this.handleResize);
    
    // æ¸…ç†å®šæ—¶å™¨
    if (this.bubbleTimer) clearTimeout(this.bubbleTimer);
    if (this.suggestionTimeout) clearTimeout(this.suggestionTimeout);
  },
  
  methods: {
    // æ‹–æ‹½ç›¸å…³æ–¹æ³•
    startDragging(event) {
      // æ›´æ–°ä¸ºæ–°çš„å…ƒç´ ç±»å
      if (event.target.closest('.assistant-container') || 
          event.target.closest('.assistant-sphere') || 
          event.target.closest('.assistant-core')) {
        this.isDragging = true;
        const assistantRect = this.floatingAssistant.getBoundingClientRect();
        this.dragOffset = {
          x: event.clientX - assistantRect.left,
          y: event.clientY - assistantRect.top
        };
        
        // åœæ­¢äº‹ä»¶å†’æ³¡
        event.stopPropagation();
      }
    },
    
    onMouseMove(event) {
      if (!this.isDragging) {
        return;
      }
      
      // æ‹–æ‹½é€»è¾‘
      const x = event.clientX - this.dragOffset.x;
      const y = event.clientY - this.dragOffset.y;
      
      // é™åˆ¶åœ¨å¯è§†åŒºåŸŸå†…
      const maxX = window.innerWidth - this.floatingAssistant.offsetWidth;
      const maxY = window.innerHeight - this.floatingAssistant.offsetHeight;
      
      const boundedX = Math.max(0, Math.min(x, maxX));
      const boundedY = Math.max(0, Math.min(y, maxY));
      
      this.floatingAssistant.style.left = boundedX + 'px';
      this.floatingAssistant.style.top = boundedY + 'px';
      this.floatingAssistant.style.bottom = 'auto';
      this.floatingAssistant.style.right = 'auto';
      
      // æ£€æŸ¥æ˜¯å¦é è¿‘è¾¹ç•Œ
      this.checkBoundaryProximity(boundedX, boundedY, maxX, maxY);
      
      // åœæ­¢äº‹ä»¶å†’æ³¡
      event.stopPropagation();
    },
    
    onMouseUp() {
      this.isDragging = false;
    },
    
    // æ£€æŸ¥è¾¹ç•Œæ¥è¿‘åº¦
    checkBoundaryProximity(x, y, maxX, maxY) {
      const threshold = 50;
      
      // æ£€æŸ¥æ˜¯å¦é è¿‘ä»»ä½•è¾¹ç•Œ
      const isNearLeft = x < threshold;
      const isNearRight = x > maxX - threshold;
      const isNearTop = y < threshold;
      const isNearBottom = y > maxY - threshold;
      
      this.assistantHidden = isNearLeft || isNearRight || isNearTop || isNearBottom;
      
      // æ ¹æ®é è¿‘çš„è¾¹ç•Œè°ƒæ•´æ ·å¼
      if (isNearLeft) {
        this.floatingAssistant.style.transform = 'translateX(-50%)';
      } else if (isNearRight) {
        this.floatingAssistant.style.transform = 'translateX(50%)';
      } else if (isNearTop) {
        this.floatingAssistant.style.transform = 'translateY(-50%)';
      } else if (isNearBottom) {
        this.floatingAssistant.style.transform = 'translateY(50%)';
      } else {
        this.floatingAssistant.style.transform = 'translate(0, 0)';
      }
    },
    
    // æ¸…ç†æ—§çš„è¡¨æƒ…è·Ÿè¸ªä»£ç 
    
    // æ˜¾ç¤º/éšè—èŠå¤©çª—å£
    toggleAssistant() {
      if (!this.isDragging) {
        this.showFloatingChat = !this.showFloatingChat;
        
        if (this.showFloatingChat) {
          this.positionChatWindow();
          
          // æ·»åŠ æ¬¢è¿æ¶ˆæ¯ï¼ˆå¦‚æœæ˜¯ç¬¬ä¸€æ¬¡æ‰“å¼€ï¼‰
          if (this.messages.length === 0) {
            this.messages.push({
              role: 'assistant',
              content: 'æ‚¨å¥½ï¼æˆ‘æ˜¯æ‚¨çš„æ™ºèƒ½å†œäº‹é—®ç­”åŠ©æ‰‹ã€‚è¯·é—®æœ‰ä»€ä¹ˆå¯ä»¥å¸®åŠ©æ‚¨çš„å†œä¸šé—®é¢˜ï¼Ÿ'
            });
          }
        }
      }
    },
    
    // å®šä½èŠå¤©çª—å£
    positionChatWindow() {
      if (!this.floatingAssistant) return;
      
      const assistantRect = this.floatingAssistant.getBoundingClientRect();
      
      // é»˜è®¤æ˜¾ç¤ºåœ¨åŠ©æ‰‹å·¦ä¾§
      let left = assistantRect.left - 350;
      let top = assistantRect.top - 450;
      
      // æ£€æŸ¥æ˜¯å¦è¶…å‡ºçª—å£è¾¹ç•Œ
      if (left < 20) left = assistantRect.right + 20;
      if (top < 20) top = 20;
      if (left + 350 > window.innerWidth) left = window.innerWidth - 370;
      if (top + 450 > window.innerHeight) top = window.innerHeight - 470;
      
      this.chatWindowTop = top;
      this.chatWindowLeft = left;
    },
    
    // æœ€å°åŒ–èŠå¤©çª—å£
    minimizeChat() {
      this.isMinimizing = true;
      this.showFloatingChat = false;
      this.assistantMinimized = true;
      
      setTimeout(() => {
        this.isMinimizing = false;
        this.assistantMinimized = false;
      }, 500);
    },
    
    // å…³é—­èŠå¤©çª—å£
    closeFloatingChat() {
      this.showFloatingChat = false;
    },
    
    // å‘é€æ¶ˆæ¯
    async sendMessage() {
      if (!this.userInput.trim() || this.isLoading) return;
      
      const question = this.userInput.trim();
      this.userInput = '';
      
      // æ·»åŠ ç”¨æˆ·æ¶ˆæ¯
      this.messages.push({
        role: 'user',
        content: question,
        timestamp: new Date().toLocaleTimeString()
      });
      
      // æ›´æ–°è¡¨æƒ…çŠ¶æ€ä¸ºæ€è€ƒ
      this.mouthState = 'thinking';
      
      // æ·»åŠ åŠ è½½ä¸­çš„åŠ©æ‰‹æ¶ˆæ¯
      this.messages.push({
        role: 'assistant',
        content: '',
        loading: true,
        error: false,
        timestamp: new Date().toLocaleTimeString()
      });
      
      // ç«‹å³æ»šåŠ¨åˆ°åº•éƒ¨
      this.scrollToBottom();
      
      this.isLoading = true;
      
      try {
        // æ¨¡æ‹ŸAPIè°ƒç”¨å»¶è¿Ÿï¼ˆå®é™…åº”ç”¨ä¸­åº”è°ƒç”¨æ–‡å¿ƒä¸€è¨€APIï¼‰
        await this.delay(1500);
        
        // è°ƒç”¨æ–‡å¿ƒä¸€è¨€APIè·å–å›ç­”
        const answer = await this.callWenxinAPI(question);
        
        // æ›´æ–°æœ€åä¸€æ¡åŠ©æ‰‹æ¶ˆæ¯
        this.messages[this.messages.length - 1] = {
          role: 'assistant',
          content: answer,
          loading: false,
          error: false,
          timestamp: new Date().toLocaleTimeString()
        };
        
        // æ›´æ–°è¡¨æƒ…ä¸ºå¾®ç¬‘
        this.mouthState = 'smile';
        
        // ä¿å­˜åˆ°å†å²è®°å½•
        this.saveToHistory();
        
      } catch (error) {
        console.error('é—®ç­”å¤±è´¥:', error);
        // æ›´æ–°ä¸ºé”™è¯¯æ¶ˆæ¯
        this.messages[this.messages.length - 1] = {
          role: 'assistant',
          content: '',
          loading: false,
          error: true,
          timestamp: new Date().toLocaleTimeString()
        };
        
        // æ›´æ–°è¡¨æƒ…ä¸ºæ€è€ƒï¼ˆå›°æƒ‘çŠ¶æ€ï¼‰
        this.mouthState = 'thinking';
      } finally {
        this.isLoading = false;
        // æ»šåŠ¨åˆ°åº•éƒ¨
        this.scrollToBottom();
      }
    },
    
    async callWenxinAPI(question) {
      try {
        // è¿™é‡Œåº”è¯¥æ˜¯å®é™…çš„æ–‡å¿ƒä¸€è¨€APIè°ƒç”¨
        // ä¸ºäº†æ¼”ç¤ºï¼Œæˆ‘ä»¬ä½¿ç”¨å¢å¼ºçš„æ¨¡æ‹Ÿå›ç­”
        return this.generateEnhancedAnswer(question);
      } catch (error) {
        console.error('APIè°ƒç”¨å¤±è´¥:', error);
        // å¦‚æœAPIè°ƒç”¨å¤±è´¥ï¼Œä½¿ç”¨å¤‡ç”¨å›ç­”ç”Ÿæˆ
        return this.generateFallbackAnswer(question);
      }
    },
    
    scrollToBottom() {
      this.$nextTick(() => {
        if (this.$el && typeof this.$el.querySelector === 'function') {
          const chatContainer = this.$el.querySelector('.chat-window-content');
          if (chatContainer) {
            chatContainer.scrollTop = chatContainer.scrollHeight;
          }
        }
      });
    },
    
    saveToHistory() {
      try {
        // ä¿å­˜åˆ°æœ¬åœ°å­˜å‚¨
        localStorage.setItem('floatingAssistantHistory', JSON.stringify(this.messages));
      } catch (error) {
        console.error('ä¿å­˜å†å²è®°å½•å¤±è´¥:', error);
      }
    },
    
    // ç”Ÿæˆæ¨¡æ‹Ÿå›ç­”
    generateEnhancedAnswer(question) {
      // å¢å¼ºçš„æ¨¡æ‹Ÿå›ç­”æ•°æ®åº“
      const answerDatabase = {
        'æ°´ç¨»ä»€ä¹ˆæ—¶å€™æ–½è‚¥æœ€å¥½ï¼Ÿ': 'æ°´ç¨»æ–½è‚¥æ—¶é—´ä¸»è¦åˆ†ä¸ºä»¥ä¸‹å‡ ä¸ªé˜¶æ®µï¼š\n1. åŸºè‚¥ï¼šåœ¨ç§»æ ½æˆ–ç›´æ’­å‰æ–½å…¥ï¼Œå æ€»æ–½è‚¥é‡çš„40-50%\n2. åˆ†è˜–è‚¥ï¼šç§»æ ½å7-10å¤©æ–½å…¥ï¼Œä¿ƒè¿›æœ‰æ•ˆåˆ†è˜–\n3. ç©—è‚¥ï¼šå¹¼ç©—åˆ†åŒ–æœŸï¼ˆç§»æ ½å40-50å¤©ï¼‰æ–½å…¥ï¼Œå æ€»æ–½è‚¥é‡çš„20-30%\n4. ç²’è‚¥ï¼šæŠ½ç©—åæ–½å…¥ï¼Œæé«˜ç»“å®ç‡å’Œåƒç²’é‡\nå…·ä½“æ–½è‚¥æ—¶é—´åº”æ ¹æ®å“ç§ã€åœŸå£¤è‚¥åŠ›å’Œç”Ÿé•¿æƒ…å†µçµæ´»è°ƒæ•´ã€‚',
